import React from "react";

const TopButton = () => (
    <div align="center" className="nav-button">
        <a href="#nav"><i className="fas fa-arrow-alt-circle-up fa-w-5 fa-3x"></i></a>
        <p>Back to Top</p>
    </div>
)

export default TopButton;


